@-webkit-keyframes bounce {
  from {
    -webkit-transform: scale(1.05) translateY(0px);
            transform: scale(1.05) translateY(0px);
  }
  to {
    -webkit-transform: scale(1.10) translateY(-15px);
            transform: scale(1.10) translateY(-15px);
  }
}

@keyframes bounce {
  from {
    -webkit-transform: scale(1.05) translateY(0px);
            transform: scale(1.05) translateY(0px);
  }
  to {
    -webkit-transform: scale(1.10) translateY(-15px);
            transform: scale(1.10) translateY(-15px);
  }
}

.poke-ball {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  width: 300px;
  height: 300px;
  -webkit-box-shadow: 0 1px 1px 0 rgba(60, 64, 67, 0.08),
    0 1px 3px 1px rgba(60, 64, 67, 0.16);
          box-shadow: 0 1px 1px 0 rgba(60, 64, 67, 0.08),
    0 1px 3px 1px rgba(60, 64, 67, 0.16);
  overflow: hidden;
  border-radius: 50%;
  cursor:pointer;
  margin-bottom: 30px;
}

.poke-ball:hover {
  -webkit-animation: bounce 0.75s infinite alternate;
          animation: bounce 0.75s infinite alternate;
}

.poke-ball-top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: start;
  padding-top: 40px;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  grid-area: 1 / 1 / 3 / 5;
  background-color: var(--main-color);
  height: 150px;
  width: 100%;
}

.poke-ball-bottom {
  grid-area: 3 / 1 / 5 / 5;
  background-color: white;
  height: 150px;
  width: 100%;
}

.ava-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  grid-area: 2 / 2 / 4 / 4;
  z-index: 1;
}

.poke-ava {
  width: 120px;
  height: 120px;
  border: 3px solid #fff;
  background-color: var(--main-color-blue);
  border-radius: 50%;
  -webkit-box-shadow: 0 1px 1px 0 rgba(60, 64, 67, 0.08),
    0 1px 3px 1px rgba(60, 64, 67, 0.16);
          box-shadow: 0 1px 1px 0 rgba(60, 64, 67, 0.08),
    0 1px 3px 1px rgba(60, 64, 67, 0.16);
}



